.popup__container {
  max-width: 430px;
  background-color: #FFF;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, .15);
  display: flex;
  flex-direction: column;
  padding: 34px 36px 36px;
  box-sizing: border-box;
  position: relative;
  margin: auto;
  align-items: center;
}

@media screen and (max-width: 600px) {
  .popup__container {
    max-width: 282px;
    padding: 25px 22px 25px;
  }
}
